export const  isEmpty = (obj) => { // check empty object
    for(let key in obj) {
        if(obj.hasOwnProperty(key))
            return false;
    }
    return true;
}

export const getListYear = () =>{
    let currentYear = new Date().getFullYear(), years = [];
    for (currentYear; currentYear >= 2000; --currentYear) {
        years.push({value: currentYear.toString(), label: 'Nm ' + currentYear})
    }
    return years;
}

