import React, { useEffect, useState, useRef, Fragment, useCallback } from "react";
import AspectRatio from "react-aspect-ratio";
import { BeatLoader } from "react-spinners";
import Network from "../services/Network";
import ConnectionClient from "./ConnectionClient";
import VideoPlayer from "react-simple-video-player";
import style from "./style.css";
import disableScroll from 'disable-scroll';

import Webcam from "react-webcam";



var socket = null;
var localStream = null;
const ratio = 4.5 / 3;

var count = 0;
var maxtime = 60;

var currentInterval = null;

let peer;



const View = (props) => {
  const server = props.config.host;
  const rqmethod = (props.config.ssl?"https://":"http://")
  const api = new Network(rqmethod+server);


  var webcamRef = useRef(null);
  const [mode, setMode] = useState(props.type);
  const [image, setImage] = useState(props.currentImg || null);
  const [loading, setLoading] = useState(true);
  const [loadingPhoto, setLoadingPhoto] = useState(false);
  const [isRecording, setIsRecording] = useState(false);
  const [timer, setTimer] = useState(0);
  const [fileID, setFileId] = useState(null);
  const [isReady, setReady] = useState(false);
  const [videoURL, setVideoURL] = useState(props.currentVideo || null);
  const [errorPermission, setErrorPermission] = useState(false);
  const [cameraType, setCameraType] = useState(false); // false: mat sau, true mat truoc

  const mediaRecorderRef = React.useRef(null);
  const [capturing, setCapturing] = React.useState(false);
  const [recordedChunks, setRecordedChunks] = React.useState([]);

  const [videoConstraints, setVideoConstraints] = React.useState({facingMode: "user"});




  useEffect(() => {
    currentInterval = null;
    disableScroll.on();
    if (mode != "preview_video" && mode != "preview_photo") {
      if(mode=="photo"){
        intPhotoCamera()
      }else{
        intVideoCamera()
      }
    } else {
      setLoading(false);
    }
    count = 0;
  }, []);





  const intPhotoCamera = async ()=>{
    try{

    }catch(e){
      console.log(e)
      setErrorPermission(true);
    }
  }
  const intVideoCamera = async ()=>{
    try{
      
    }catch(e){
      console.log(e)
      setErrorPermission(true);
    }
  }


  const handleStartCaptureClick = React.useCallback(() => {
    console.log("handleStartCaptureClick")
    setCapturing(true);
    mediaRecorderRef.current = new MediaRecorder(webcamRef.current.stream, {
      mimeType: "video/webm"
    });
    mediaRecorderRef.current.addEventListener(
      "dataavailable",
      handleDataAvailable
    );
    mediaRecorderRef.current.start();
  }, [webcamRef, setCapturing, mediaRecorderRef]);

   const handleDataAvailable = React.useCallback(
    ({ data }) => {
      if (data.size > 0) {
        const temp = recordedChunks.concat(data)
        setRecordedChunks(temp);
        if (temp.length) {
          const blob = new Blob(temp, {
            type: "video/webm"
          });
          // console.log("blob ", blob)
          const url = URL.createObjectURL(blob);
          setVideoURL(`${url}`);
        }
      }
    },
    [setRecordedChunks]
  );



  const handleStopCaptureClick = React.useCallback(() => {
    console.log("handleStopCaptureClick")
    mediaRecorderRef.current.stop();
    setCapturing(false);
  }, [mediaRecorderRef, webcamRef, setCapturing]);

  // const handleDownload = React.useCallback(() => {
  //    console.log("Download video 1", recordedChunks.length)
  //   if (recordedChunks.length) {
  //     console.log("Download video")
  //     const blob = new Blob(recordedChunks, {
  //       type: "video/webm"
  //     });
  //     const url = URL.createObjectURL(blob);
  //     const a = document.createElement("a");
  //     document.body.appendChild(a);
  //     a.style = "display: none";
  //     a.href = url;
  //     a.download = "react-webcam-stream-capture.webm";
  //     a.click();
  //     window.URL.revokeObjectURL(url);
  //     setRecordedChunks([]);
  //   }
  // }, [recordedChunks]);


  const formatTimer = (sec_num) => {
    var minutes = Math.floor(sec_num / 60);
    var seconds = sec_num - minutes * 60;
    if (minutes < 10) {
      minutes = "0" + minutes;
    }
    if (seconds < 10) {
      seconds = "0" + seconds;
    }
    return minutes + ":" + seconds;
  };
  const countUp = () => {
    if (count < maxtime) {
      count++;
      if(count > 1){
         setReady(true)
      }
      setTimer(count);
    } else {
      //peerConnection.close();
      setIsRecording(false);
      clearInterval(currentInterval);
    }
  };
  const swapCamera = () => {
    if(isReady){
      setCameraType(!cameraType);
      videoConstraints.facingMode = cameraType ? "user" : "environment"
      setVideoConstraints({...videoConstraints})
      
    }
  };


  const startRecord = ()=>{
     setReady(false)
     handleStartCaptureClick()
     
  }

  const stopRecord = ()=>{
    handleStopCaptureClick()
  }


  const capture = () => {
    const imageSrc = webcamRef.current.getScreenshot();
    setMode("preview_photo");
    setImage(imageSrc);
  };

  const reCapture = () => {
    initCamera()
    setLoading(true);
    setImage(null);
    setMode("photo");
  };

  const reRecord = () => {
    initCamera()
    setLoading(true);
    setVideoURL(null);
    setMode("video");
    setIsRecording(false);
  };

  const initCamera = () => {
     //initMediaTransporter()
  }
  const onUserMedia = ()=>{
    setReady(true)
    setTimeout(() => {
       setLoading(false)
    }, 1500);

   
  }
  const backCamera = ()=>{
    if(localStream){
      localStream.getTracks().forEach((track) => track.stop());
    }
    disableScroll.off();
    props.onBackCamera()
  }
  const closeCamera = () => {
    if(localStream){
      localStream.getTracks().forEach((track) => track.stop());
    }
    disableScroll.off();
    props.onCloseCamera(props.type, props.mediaId, peer.sessionId);
  };



  const useThisPhoto = async () => {
    try {
      if (props.currentImg == image) {
        const file_key = props.currentImg.split("/")[
          props.currentImg.split("/").length - 1
        ];
        backCamera();
      } else {
        setLoadingPhoto(true);
        if (props.mediaId.includes("f_")) {
          const rs = await api.post(
            `/api/media-transshipment/upload-other/${props.transId}`,
            { data: image, media_id: props.mediaId }
          );
          setLoadingPhoto(false);
          props.onCloseCamera(props.type, props.mediaId, rs.file_key);
        } else {
          const rs = await api.post(
            `/api/media-transshipment/upload/${props.transId}`,
            { data: image, media_id: props.mediaId }
          );
          setLoadingPhoto(false);
          disableScroll.off()
          props.onCloseCamera(props.type, props.mediaId, rs.file_key);
        }
      }
    } catch (e) {
      setLoadingPhoto(false);
      console.log(e);
    }
  };

  const useThisVideo = async () => {
    try {
      if (props.currentVideo == videoURL) {
          backCamera();
      } else {
        setLoadingPhoto(true);

         if (recordedChunks.length) {
          const blob = new Blob(recordedChunks, {
            type: "video/webm"
          });
          // console.log("blob ", blob)
          const url = URL.createObjectURL(blob);
          var formData = new FormData();
          formData.append('video', blob); 
          try{
            setLoading(true)
               const rs = await api.postFormData(`/api/media-transshipment/upload-video/${props.transId}/${props.mediaId}`, formData);
               console.log(rs)
               setTimeout(() => {
                 disableScroll.off();
                  props.onCloseCamera(props.type, props.mediaId, rs.fileId);
                  setLoadingPhoto(false);
                  setLoading(false)
                }, 500);

            }catch(e){
              console.log(e)
            }

          setRecordedChunks([]);
        }



        // const rs = await api.post(
        //   `/api/wrtc/${props.transId}/commit-video/${props.mediaId}`,
        //   {
        //     file_id: peer.sessionId,
        //   }
        // );
        
      }
    } catch (e) {
      setLoadingPhoto(false);
      console.log(e);
    }
  };


  const onRecordChange = async (e) => {
    if(isReady){
      const checked = e.target.checked;
      setIsRecording(checked);
      if (checked) {
        currentInterval = setInterval(countUp, 1000);
       // createConnection();
        startRecord()
        console.log("Start Streaming")
      } else {
        //peerConnection.close();
        stopRecord()
        // localStream.getTracks().forEach((track) => track.stop());
        console.log("Stop Streaming")
        setLoading(true);
        clearInterval(currentInterval);
        setMode("preview_video");
        setTimeout(() => {
          // localStream.getTracks().forEach((track) => track.stop());
          // setVideoURL(`${rqmethod+server}/media_cache/${props.transId}/video-${peer.sessionId}.webm`);
          // console.log("Load video: ",`${rqmethod+server}/media_cache/${props.transId}/video-${peer.sessionId}.webm`)
          // handleDownload()
          setLoading(false);
        }, 1500);
      }
    }
    
  };

  const renderCMR = () => {
    if (image && mode == "preview_photo") {
      return (
        <img
          src={image}
          alt="Image preview"
          className={style.img_camera_preview}
        />
      );
    } else if (videoURL && mode == "preview_video") {
      return <VideoPlayer url={videoURL} autosize={true} loop={true} />;
    } else {
      return (
        <Webcam 
          className={cameraType?`${style.camera_frame_dicodes} ${style.cameraFlipX}`:`${style.camera_frame_dicodes}`}
          videoConstraints={videoConstraints}
          onUserMediaError={(e)=>setErrorPermission(true)}
          onUserMedia={()=>onUserMedia()}
          ref={webcamRef}
        />
      );
    }
  };
  const renderButton = () => {
    if (image && mode == "preview_photo") {
      return (
        <div
          className={
            image ? style.bottom_camera_menu : style.bottom_camera_menu_hide
          }
        >
          <div className="row">
            <div className="col-6">
              <button className={style.recapture} onClick={() => reCapture()}>
                Chp li
              </button>
            </div>
            <div className="col-6">
              <button
                className={
                  props.currentImg == image
                    ? style.cancel_photo
                    : style.use_photo
                }
                onClick={useThisPhoto}
                disabled={loadingPhoto}
              >
                {loadingPhoto ? (
                  <div>Uploading...</div>
                ) : props.currentImg == image ? (
                  <div>Thot</div>
                ) : (
                  <div>S dng</div>
                )}
              </button>
            </div>
          </div>
        </div>
      );
    } else if (videoURL && mode == "preview_video") {
      return (
        <div
          className={
            videoURL ? style.bottom_camera_menu : style.bottom_camera_menu_hide
          }
        >
          <div className="row">
            <div className="col-6">
              <button className={style.recapture} onClick={() => reRecord()}>
                Quay li
              </button>
            </div>
            <div className="col-6">
              <button
                className={
                  props.currentImg == image
                    ? style.cancel_photo
                    : style.use_photo
                }
                onClick={useThisVideo}
                disabled={loadingPhoto}
              >
                {loadingPhoto ? (
                  <div>Uploading...</div>
                ) : props.currentVideo == videoURL ? (
                  <div>Thot</div>
                ) : (
                  <div>S dng</div>
                )}
              </button>
            </div>
          </div>
        </div>
      );
    }
  };

  return (
    <div className={style.xxx_camera}>
      <div
        className={
          mode == "preview_photo"
            ? style.top_camera_menu_hide
            : style.top_camera_menu
        }
      >
        <div className={style.horz_progressbar}>
          <div
            className={
              isRecording
                ? style.horz_progressbar_item_icoxxxbar
                : style.horz_progressbar__item_icoxxxbar_run
            }
          />
        </div>
        <div className="row">
          <div className="col-4">
            <div className={style.back_camera} onClick={() => backCamera()}>
              <i className="fas fa-chevron-left"></i>
            </div>
          </div>
          <div className={`col-4 ${style.cl_label_camera}`}>
            {mode == "photo" ? (
              <span>Chp nh</span>
            ) : (

              <React.Fragment>
                {(videoURL==null)&&<div>
                  <span>{formatTimer(timer)}</span>
                  <div className={style.dot_record} />
                </div>}
              </React.Fragment>
            )}
          </div>
          <div className="col-4">
            {(videoURL==null)&&<div className={style.switch_camera} onClick={() => swapCamera()}>
              <i className="fas fa-sync"></i>
            </div>}
          </div>
        </div>
      </div>
      <div
        className={
          mode == "preview_photo" || mode == "preview_video"
            ? style.bottom_camera_menu_hide
            : style.bottom_camera_menu
        }
      >
        <div className="row">
          <div className="col-4"></div>
          <div className="col-4">
            {mode == "photo" ? (
              <div
                className={`${style.button_camera_capture} ${style.btn_photo}`}
                onClick={() => capture()}
              >
                <i className="fas fa-camera"></i>
              </div>
            ) : (
              <div className="">
                <input
                  type="checkbox"
                  name="checkbox"
                  className={style.btn_record}
                  onChange={onRecordChange}
                  id="checkbox"
                  checked={isRecording}
                />
                <label htmlFor="checkbox"></label>
              </div>
            )}
          </div>
          <div className="col-4"></div>
        </div>
      </div>

      {renderCMR()}

      {loading && (
        <div className={style.loading_bs}>
            <div className={style.loading_box}>
              <BeatLoader color={"#dfe6e9"} />
            </div>
        </div>
      )}

      {renderButton()}

      {errorPermission && (
        <div className={style.error_permission}>
          <div className={style.permission_div}>
            <i
              className={`fas fa-exclamation-triangle ${style.icon_permission}`}
            ></i>
            <p className={style.per_i1}>Camera permission denied!</p>
            <p>Vui lng cho php quyn truy cp camera v reload li trang</p>
          </div>
        </div>
      )}
    </div>
  );
};
export default View;
