import { Button, Form, Row, Col } from "react-bootstrap";
import cogoToast from 'cogo-toast';
import React, { useEffect, useState, createRef } from "react";
import PhotoItem from "./camera/photoitem.jsx";
import Camera from "./camera/Camera.jsx";
import Network from "./services/Network";
import { SyncLoader } from "react-spinners";
import ErrorScreen from "./view/ErrorScreen";
import style from "./styles.module.css";
import cancelIcon from "remove.png";
import Slider from "react-slick";
import left_ac from "left_ac.png";
import right_ac from "right_ac.png";

const settings = {
  dots: false,
  infinite: true,
  arrows: false,
  speed: 500,
  slidesToShow: 3,
  slidesToScroll: 3,
  className: style.other_img_slide,
  infinite: false,
};
const MediaTransshipment = (props) => {
  var slider = createRef();
  const config = props.config;
  const rqmethod = (config.ssl?"https://":"http://")
  const api = new Network(rqmethod+config.host);

  const [transhipmentId, setTransshipmentId] = useState(config.trans_id);
  const [transhipmentLoading, setTransshipmentLoading] = useState(true);
  const [transhipmentInitError, setTransshipmentInitError] = useState(false);
  const [isActiveSend, setActiveSend] = useState(false);
  const [mediaLayout, setMediaLayout] = useState([]);

  const [mediaResponse, setMediaResponse] = useState({});

  const [otherPhoto, setOtherPhoto] = useState({}); // mang luu other photo

  // const scrollY = useScrollPosition(60)
  const [showCamera, setShowCamera] = useState(null);
  const [photoId, setPhotoId] = useState(null);
  const [mediaObject, setMediaObject] = useState(null);
  const [loading, setLoading] = useState(false);
  const [currentImg, setcurrentImg] = useState(null);
  const [currentVideo, setcurrentVideo] = useState(null);

  const getTransshipmentInfo = async (transID) => {
    try {
      const result = await api.get(`/api/media/transshipment/${transID}`);
      if (result.layout) {
        if (result.media_object) {
          setMediaObject(result.media_object);
          if(result.media_object.other){
             {Object.keys(result.media_object.other).map((imgKey, index) => {
                otherPhoto[imgKey] = result.media_object.other[imgKey]
             })}
            setOtherPhoto(otherPhoto)
          }
          var initObject = {};
          for (const [key, value] of Object.entries(result.media_object)) {
            if (value.file) {
              initObject[key] = value;
            }
          }
          setMediaResponse(initObject);
          const isValid = validForm();

          if (isValid) {
            setActiveSend(true);
          }
        }
        setMediaLayout(result.layout);
      } else {
        setTransshipmentInitError(true);
      }
      setTransshipmentLoading(false);
    } catch (e) {
      console.log("Loi");
      setTransshipmentLoading(false);
      setTransshipmentInitError(true);
    }
  };

  const onBackCamera = () => {
    setShowCamera(null);
  };


  const onCloseCamera = (type, mediaId, fileId) => {
    if (mediaId.includes("f_")) {
      const newData = otherPhoto;
      if (fileId) {
        newData[mediaId] = {
          type: type,
          file: fileId,
          format: type=="photo"?"png":"webm"
        }
      }
      const ordered = Object.keys(newData)
        .sort(function (a, b) {
          return b.localeCompare(a);
        })
        .reduce((obj, key) => {
          obj[key] = newData[key];
          return obj;
        }, {});
      // console.log(ordered);
      setOtherPhoto({ ...ordered });
    } else {
      const newData = mediaResponse;
      if (fileId) {
        newData[mediaId] = {
          type: type,
          file: fileId,
          format: type=="photo"?"png":"webm"
        };
      }
      setMediaResponse({ ...mediaResponse, ...newData });
    }
    setShowCamera(null);
    const isValid = validForm();
    if (isValid) {
      setActiveSend(true);
    }
  };

  const validForm = () => {
    var missmatch = false;
    mediaLayout.map((parent, index_parent) => {
      parent.map((child, index_child) => {
        if (mediaResponse[child.id] == null) {
          missmatch = true;
        }
      });
    });
    return !missmatch;
  };

  const openCamera = (type, id, currentMedia) => {
    if (type == "preview_photo" && currentMedia) {
      setcurrentImg(currentMedia);
    } else if (type == "preview_video" && currentMedia) {
      setcurrentVideo(currentMedia);
    }
    setShowCamera(type);
    setPhotoId(id);
  };

  const submit = async () => {
    try {
      const response = await api.post(`/api/media/transshipment/emit/${transhipmentId}`, {data: mediaResponse})

      if (props.onSubmit) {
        mediaResponse.other = otherPhoto
        props.onSubmit(mediaResponse, transhipmentId);
      }
    } catch (e) {
      cogoToast.error('Submit media failed!');
      console.log("Loi ", e);
    }
  };

  const deleteOtherMedia = async (media_id)=>{
    console.log("Delete >> ", media_id)
    try {
      const response = await api.get(`/api/wrtc/${transhipmentId}/delete-other-media/${media_id}`)
      if(response.data.delete){
          delete otherPhoto[media_id]
          setOtherPhoto({...otherPhoto})
      }else{
        cogoToast.error('Delete media failed!');
      }

    } catch (e) {
      console.log("Loi");
    }

  }

  useEffect(() => {
    mediaResponse.id = config.trans_id;
    setTransshipmentId(config.trans_id);
    getTransshipmentInfo(config.trans_id);
  }, []);
  const isEmpty = (obj) => {
    for (var prop in obj) {
      if (obj.hasOwnProperty(prop)) {
        return false;
      }
    }
    return JSON.stringify(obj) === JSON.stringify({});
  };
  return (
    <div className={showCamera?style.disablescroll:style.media_transshipment_container}>
      {transhipmentLoading && (
        <div className={style.transshipment_loading_page}>
          <div className={style.transshipment_loading_page_jld}>
            <SyncLoader color={"#329945"} />
          </div>
        </div>
      )}

      {transhipmentInitError && <ErrorScreen />}




      <div className={(showCamera==null)?style.galery_container:style.galery_container_hidden}>
        {mediaLayout.map((parent_layout, index) => {
          return (
            <Row key={index}>
              {parent_layout.map((layout, index2) => {
                const previewpostfix =
                  layout.type == "video"
                    ? `/api/media/cache/videothumbnail/${transhipmentId}/`
                    : `/api/media/cache/photothumbnail/${transhipmentId}/`;
                return (
                  <Col key={index2}>
                    <p className={style.photo_title}>{layout.title}</p>
                    <PhotoItem
                      dthumb={
                        mediaResponse[layout?.id]
                          ? rqmethod + config.host +
                            previewpostfix +
                            mediaResponse[layout?.id]?.file
                          : layout?.thumb
                      }
                      isVideo={layout?.type == "video"}
                      onClick={() =>
                        openCamera(
                          mediaResponse[layout?.id]
                            ? `preview_${layout?.type}`
                            : layout?.type,
                          layout?.id,
                          layout?.type == "video"
                            ? mediaResponse[layout?.id]
                              ? `${rqmethod+config.host}/media_cache/${transhipmentId}/video-${
                                  mediaResponse[layout?.id]?.file
                                }.webm`
                              : null
                            : mediaResponse[layout.id]?.file
                            ? rqmethod+config?.host +
                              previewpostfix +
                              mediaResponse[layout?.id]?.file
                            : null
                        )
                      }
                    />
                    <p className={style.photo_note}>
                      Chm vo hnh  bt Camera
                    </p>
                  </Col>
                );
              })}
            </Row>
          );
        })}

        <div className={style.footer_transshipment}>
          {config.note_label&&<p>{config.note_label}</p>}
          <div className={style.line_1}></div>
          <p>Cc nh khc</p>
          {isEmpty(otherPhoto) ? (
            <div
              className={style.add_another_photo}
              onClick={() =>
                openCamera("photo", `f_${new Date().getTime()}`, null)
              }
            >
              <div>
                <i className="fas fa-camera"></i> <span>Chp nh</span>
              </div>
            </div>
          ) : (
            <div className={style.row_photo}>
              <div className="row">
                <div className="col-3">
                  <div className="position-relative pt-1">
                    <div
                      className={style.camera_item}
                      onClick={() =>
                        openCamera("photo", `f_${new Date().getTime()}`, null)
                      }
                    >
                      <i className="fas fa-camera"></i>
                    </div>
                  </div>
                </div>
                <div className="col-9 p-0 position-relative">
                  <div
                    style={
                      Object.keys(otherPhoto).length > 3
                        ? {}
                        : { display: "none" }
                    }
                    className={style.arleft}
                    onClick={() => slider.slickPrev()}
                  >
                    <img src={left_ac} />
                  </div>
                  <Slider {...settings} ref={(c) => (slider = c)}>
                    {Object.keys(otherPhoto).map((imgKey, index) => {
                      const previewpostfix = `/api/media/cache/photothumbnail/${transhipmentId}/`;
                      return (
                        <div className="col" key={imgKey}>
                          <div className="position-relative pt-1">
                            <div className={style.cancel_icon}>
                              <img src={cancelIcon} onClick={(e)=>deleteOtherMedia(imgKey)}/>
                            </div>
                            <div
                              className={style.photo_item_other}
                              onClick={() =>
                                openCamera(
                                  "preview_photo",
                                  imgKey,
                                  rqmethod+config.host +
                                    previewpostfix +
                                    otherPhoto[imgKey].file
                                )
                              }
                            >
                              <div className={style.wrap_img_other}>
                                <img
                                  className={style.img_other}
                                  src={
                                    rqmethod+config.host +
                                    previewpostfix +
                                    otherPhoto[imgKey].file
                                  }
                                />
                              </div>
                            </div>
                          </div>
                        </div>
                      );
                    })}
                  </Slider>

                  <div
                    style={
                      Object.keys(otherPhoto).length > 3
                        ? {}
                        : { display: "none" }
                    }
                    className={style.arright}
                    onClick={() => slider.slickNext()}
                  >
                    <img src={right_ac} />
                  </div>
                </div>

              </div>
            </div>
          )}

          <Button
            onClick={submit}
            className={
              isActiveSend
                ? style.footer_transshipment_button
                : style.footer_transshipment_button
            }
            disabled={!validForm()}
          >
            Tip tc
          </Button>
        </div>
      </div>




      {showCamera && (
        <Camera
          config={config}
          transId={transhipmentId}
          onCloseCamera={onCloseCamera}
          onBackCamera={onBackCamera}
          mediaId={photoId}
          type={showCamera}
          currentImg={currentImg}
          currentVideo={currentVideo}
        />
      )}
    </div>
  );
};

export default MediaTransshipment;
