import React, { useEffect, useState } from "react";
import AspectRatio from 'react-aspect-ratio';
import { SyncLoader } from 'react-spinners';
import style from "./style.css"


const imgCameraFrame = "/assets/sdk/camera-frame.svg"
const imgVideo = "/assets/sdk/btn-video.svg"
const imgCamera = "/assets/sdk/btn-camera.svg"
const ItemPhotoView = (props) => {
 const [image, setImage] = useState(props.dthumb);
 const [loading, setLoading] = useState(false);
 useEffect(() => {
 	if(props.dthumb!=image){
 		setLoading(true)

 		setTimeout(
		  () => {
		  	setImage(props.dthumb)
		  	setTimeout(
		      () => {
		        setLoading(false)
		      },
		      props.isVideo?1000:500
		    );
		  },
		  props.isVideo?2000:500
		);


 	}

 }, [props.dthumb]);

  return (
  	<AspectRatio ratio="3/4" img={image} onClick={()=>props.onClick()}>
	    <div className={style.photo_item}>
	     	<img className={style.photo_item_frame} src={imgCameraFrame} alt="aa"/>
			<img className={style.im_thumbnail} src={image} />
			<div className={loading?`${style.photo_item_loading} ${style.photo_item_loading_show}`:`${style.photo_item_loading}`}><div className={style.acx2loading}><SyncLoader color={"#329945"}/></div></div>{!loading&&<img className={style.photo_item_btn_camera} src={props.isVideo?imgVideo:imgCamera} />}
	    </div>
     </AspectRatio>
  );
};
export default ItemPhotoView;
