
import React, { useEffect, useState } from "react";
// import 'react-modern-calendar-datepicker/lib/DatePicker.css';
import { Calendar } from "react-modern-calendar-datepicker";

const Main = (props) => {
    const [selectedDay, setSelectedDay] = useState(props.selectedDay);
    const itemSelect = (date)=>{
        setSelectedDay(date)
        setTimeout(() => {
            props.dateSelect(date)
        }, 200);
    }

    useEffect(() =>{
        console.log('props.selectedDay', props.selectedDay);
    },[props.selectedDay])

    const myCustomLocale = {
        months: [
            'Thng 1',
            'Thng 2',
            'Thng 3',
            'Thng 4',
            'Thng 5',
            'Thng 6',
            'Thng 7',
            'Thng 8',
            'Thng 9',
            'Thng 10',
            'Thng 11',
            'Thng 12',
        ],
        weekDays: [
            {
                name: 'Sunday', // used for accessibility
                short: 'Th2', // displayed at the top of days' rows
                isWeekend: true, // is it a formal weekend or not?
            },
            {
                name: 'Monday',
                short: 'Th3',
            },
            {
                name: 'Tuesday',
                short: 'Th4',
            },
            {
                name: 'Wednesday',
                short: 'Th5',
            },
            {
                name: 'Thursday',
                short: 'Th6',
            },
            {
                name: 'Friday',
                short: 'Th7',
            },
            {
                name: 'Saturday',
                short: 'CN',
                isWeekend: true,
            },
        ],
        // just play around with this number between 0 and 6
        weekStartingIndex: 0,
        // return a { year: number, month: number, day: number } object
        getToday(gregorainTodayObject) {
            return gregorainTodayObject;
        },

        // return a native JavaScript date here
        toNativeDate(date) {
            return new Date(date.year, date.month - 1, date.day);
        },
        // return a number for date's month length
        getMonthLength(date) {
            return new Date(date.year, date.month, 0).getDate();
        },
        // return a transformed digit to your locale
        transformDigit(digit) {
            return digit;
        },
        // texts in the date picker
        nextMonth: 'Next Month',
        previousMonth: 'Previous Month',
        openMonthSelector: 'Open Month Selector',
        openYearSelector: 'Open Year Selector',
        closeMonthSelector: 'Close Month Selector',
        closeYearSelector: 'Close Year Selector',
        defaultPlaceholder: 'Select...',
        // for input range value
        from: 'from',
        to: 'to',
        // used for input value when multi dates are selected
        digitSeparator: ',',

        // if your provide -2 for example, year will be 2 digited
        yearLetterSkip: 0,

        // is your language rtl or ltr?
        isRtl: false,
    }

    return (
        <div className="float-pophover">
            <div className="popheader">
                <p className="stitle">{props.label}</p>
            </div>
            <div className="select-body datw">
                <Calendar
                    value={selectedDay}
                    onChange={itemSelect}
                    minimumDate={props.minimumDate||null}
                    maximumDate={props.maximumDate||null}
                    locale={myCustomLocale}
                    shouldHighlightWeekends
                />
            </div>
        </div>
    );
};

export default Main;
