
import React, {useRef, useState} from "react";
import {Form, Overlay, Tooltip} from "react-bootstrap";
import Select from "react-select";


const SelectBase = (props) =>{
    const [show, setShow] = useState(false);
    const target = useRef(null);

    const styleSelect = {
        control: base => ({
            ...base,
            height: 38,
            background: '#FFFFFF',
        })
    };
    return(
        <div id="cus-input-label">
            <Form.Group>
                <Form.Label>{props.label}
                    {props.required?<span style={{color:"#DA2128"}}>*</span>:""}
                    {props.tooltip? (
                        <>
                        <span className="show-tooltip-select" ref={target} onClick={() => setShow(!show)}>
                            <i className="fas fa-info-circle" />
                        </span>
                        <Overlay
                            rootClose={true}
                            onHide={() => setShow(false)}
                            target={target.current} show={show} placement="bottom">
                            {(props) => (
                                <Tooltip id="overlay-example" {...props}>
                                    <p>
                                        <p className="text-center">
                                            <strong>M T PHM VI BO HIM</strong>
                                        </p>
                                        <p style={{ marginBottom: "10px" }}>
                                            1. Phm vi bo him A (Bo him ny cung cp c lp):
                                            NBH b tai nn l nguyn nhn trc tip khin cho
                                            NBH b t vong hoc b thng tt ton b vnh vin
                                            t 80% tr ln;
                                        </p>
                                        <p>
                                            2. Phm vi bo him B (Bo him ny ch cung cp khi
                                            NBH tham gia phm vi bo him A): NBH b m au,
                                            bnh tt, thai sn l nguyn nhn trc tip khin cho
                                            NBH b t vong hoc b thng tt ton b vnh vin
                                            t 80% tr ln;
                                        </p>
                                        <p>
                                            3. Phm vi bo him C (Bo him ny ch cung cp khi
                                            NBH tham gia phm vi bo him A): Ngi c bo him
                                            b Ta n c thm quyn tuyn b mt tch theo quy
                                            nh php lut Vit Nam
                                        </p>
                                    </p>

                                </Tooltip>
                            )}
                        </Overlay>
                        </>
                    ) : ''}
                </Form.Label>
                <Select
                    {...props}
                    isDisabled={props.disable}
                    classNamePrefix='cus-select'
                    // defaultValue={defaultVl}
                    value={props.value}
                    onChange={props.onChange}
                    options={props.options}
                    styles={styleSelect}
                />
            </Form.Group>
        </div>
    )
}

export default SelectBase;
