import axios from 'axios';
var self = null
export default class Networking {
  constructor(serverhost) {
    this.domain = serverhost
    this.post = this.post.bind(this)
    this.postFormData = this.postFormData.bind(this)
    this.patch = this.patch.bind(this)
    this.get = this.get.bind(this)
    this.handleError = this.handleError.bind(this)
    this.getHeader = this.getHeader.bind(this)

    self = this
  }

  	getHeader(){
  		return {
  			headers: {
  			 'Access-Control-Allow-Headers': 'Content-Type',
  			 'Access-Control-Allow-Methods': 'POST',
  			 "Content-Type": "application/json",
  			 'Access-Control-Allow-Origin': '*',  }
  			}
  	}
	post(url, params, callback){
		return new Promise((resolve, rejected)=>{
		 	axios.post(this.domain+url, params, this.getHeader()).then(function (response) {
		    	return resolve(response.data)
			  })
			  .catch(function (error) {
			  	 return rejected(error)
			})
		 })
	  }
	postFormData(url, bodyFormData){
		return new Promise((resolve, rejected)=>{
		 	axios({
			  method: "post",
			  url: this.domain+url,
			  data: bodyFormData,
			  headers: { "Content-Type": "multipart/form-data" },
			})
			  .then(function (response) {
			    return resolve(response.data)
			  })
			  .catch(function (response) {
			   return rejected()
			  });
		 })
	}

	patch(url, params){
	 	return new Promise((resolve, rejected)=>{
		 	axios.patch(this.domain+url, params).then(function (response) {
		    	return resolve(response.data)
			  })
			  .catch(function (error) {
			  	console.log(error)
			    self.handleError(error, rejected)
			})
		})
	  }

	get(url){
		return new Promise((resolve, rejected)=>{
			axios.get(this.domain+url).then(function (response) {
	 			return resolve(response.data)
			  })
			  .catch(function (error) {
			  	console.log(error)
			    self.handleError(error, rejected)
			})
		})
	  }
	 delete(url){
	 	return new Promise((resolve, rejected)=>{
		 	axios.delete(this.domain+url).then(function (response) {
		 		return resolve(response.data)
			}).catch(function (error) {
			     self.handleError(error, rejected)
			})
		})
	  }

	  handleError(error, callback){
	  	if (error.response) {
		  	if(error.response.status==401){
		  		
		  	}else if(error.response.status==403){
		  	
		  	}else{
		  		console.log("ERROR---->",error)
		  		callback(error, null)
		  	}
		}else{
			console.log("ERROR---->",error)
		  callback(error, null)
		}
	  }



}
